html{overflow:scroll;overflow-x:hidden}html body{background-color:black;margin:0;font-family:'Raleway', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}::-webkit-scrollbar{width:0px;background:transparent}::-webkit-scrollbar-thumb{background:#FF0000}

.main-banner{margin-top:40px}.main-banner .hello{color:#f5f5f5;font-size:60px;font-weight:bold}.main-banner .subHeader{color:#f5f5f5;font-size:25px;font-weight:bold}.main-banner .txt-rotate>.wrap{border-right:0.08em solid #666}@media screen and (max-width: 500px){.main-banner{margin-top:10px}.main-banner .hero-image{width:50%}.main-banner .hello{font-size:50px;line-height:48px;margin:12px 0}.main-banner .subHeader{font-size:30px;line-height:45px;font-weight:400}}img.my-avatar,img.my-half{width:100%;opacity:0.75;border-radius:50%;border:24px solid #f5f5f5}img.hero-image{width:20%}@media screen and (max-width: 500px){img.hero-image{width:65%}}

header{position:-webkit-sticky;position:sticky;padding:8px 0}header .header-main{color:whitesmoke;font-size:24px;font-weight:400;text-align:center}header .header-main .header-button{background-color:transparent;color:#f5f5f5;margin:8px;text-decoration:none;border-radius:8px;padding:8px;font-size:18px;border:1px solid #f5f5f5}header .header-main .header-button:hover,header .header-main .header-button:focus{background-color:#f5f5f5;color:black}header .side-bar{text-align:end;margin:auto}header .side-bar .btn{color:#f5f5f5;font-size:24px;background-color:#f5f5f5;border:none;margin:8px}@media screen and (max-width: 500px){header .side-bar .btn{display:block;text-align:left}}header .small-logo{text-align:left;padding:0 16px;font-size:30px;font-weight:bold;color:whitesmoke}header .nochange{text-align:end;margin:auto}header .nochange .bar1,header .nochange .bar2,header .nochange .bar3{width:35px;height:2px;background-color:#f5f5f5;margin:6px 0;transition:0.4s}header .nochange .header-button{display:none}header .change{text-align:end;margin:auto}header .change .bar1{width:35px;height:2px;background-color:#f5f5f5;margin:6px 0;transform:rotate(-45deg) translate(-2px, 6px);transition:0.4s}header .change .bar2{opacity:0}header .change .bar3{width:35px;height:2px;background-color:#f5f5f5;margin:6px 0;transform:rotate(45deg) translate(0px, -4px);transition:0.4s}header .change .wrapper{margin:24px 0;text-align:center}header .change .wrapper .header-button{background-color:transparent;color:#f5f5f5;margin:8px;text-decoration:none;border-radius:8px;padding:8px;font-size:18px;border:1px solid #f5f5f5}header .change .wrapper .header-button:hover,header .change .wrapper .header-button:focus{background-color:#f5f5f5;color:black}

